export function authHeader() {
    // return authorization header with jwt token
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.token) {
        return { 'Authorization': 'Token ' + user.token };
    } else {
        return {};
    }
}

export function isAuthorized() {
    let user = JSON.parse(localStorage.getItem('user'));
    if (user) return true;
    return false;
}